import React from 'react'
import { Card } from 'reactstrap'
import themeConfig from '../../configs/themeConfig'
import { ArrowRightCircle } from 'react-feather'

class DashboardCards extends React.Component {
  render() {
    const Dark = themeConfig.theme === 'dark'

    const addClass = () => {
      document
        .getElementById(`back-image-${this.props.index}`)
        .classList.add('back-image-scale')
      document
        .getElementById(`card-title-${this.props.index}`)
        .classList.add('card-title-div-up')
      document
        .getElementById(`description-div-${this.props.index}`)
        .classList.add('description-div-up')
      document.getElementById(`card-title-no-${this.props.index}`).style.color =
        Dark ? 'white' : 'white'
    }
    const removeClass = () => {
      document
        .getElementById(`back-image-${this.props.index}`)
        .classList.remove('back-image-scale')
      document
        .getElementById(`card-title-${this.props.index}`)
        .classList.remove('card-title-div-up')
      document
        .getElementById(`description-div-${this.props.index}`)
        .classList.remove('description-div-up')
      document.getElementById(`card-title-no-${this.props.index}`).style.color =
        Dark ? 'white' : 'black'
    }

    return (
      <Card
        className="whole-card"
        onMouseEnter={addClass}
        onMouseLeave={removeClass}
      >
        <div
          id={`back-image-${this.props.index}`}
          className={
            Dark
              ? 'back-image-dark back-image-dark-normal'
              : 'back-image back-image-normal'
          }
          style={{
            backgroundImage: `url(${this.props.background})`,
            backgroundSize: (() => {
              if (this.props.stat === 'Assets') {
                return '220px'
              } else if (this.props.stat === 'Vault') {
                return '220px'
              } else if (this.props.stat === 'Nominees') {
                return '185px'
              } else if (this.props.stat === 'Daily Spends') {
                return '185px'
              } else if (this.props.stat === 'Documents') {
                return '210px'
              } else if (this.props.stat === 'Sender') {
                return '250px'
              }
            })(),
            backgroundPosition: (() => {
              if (this.props.stat === 'Vault') {
                return '125px'
              } else if (this.props.stat === 'Nominees') {
                return '165px'
              } else if (this.props.stat === 'Daily Spends') {
                return '175px'
              } else if (this.props.stat === 'Secret Diary') {
                return '165px'
              } else if (this.props.stat === 'Sender') {
                return '110px'
              } else if (this.props.stat === 'Assets') {
                return '125px'
              }
            })()
          }}
        />
        <div className="card-title-div" id={`card-title-${this.props.index}`}>
          <span
            id={`card-title-no-${this.props.index}`}
            style={{ color: Dark ? 'white' : 'black' }}
            className="card_title"
          >
            {this.props.stat}
          </span>
        </div>
        <div
          className="description-div d-flex justify-content-between"
          id={`description-div-${this.props.index}`}
          style={{ backgroundColor: Dark ? 'lightgrey' : 'lightgrey' }}
        >
          <div>
            <h2
              className="card_description"
              style={{ color: Dark ? 'black' : 'black' }}
            >
              {this.props.statTitle}
            </h2>
          </div>
          <div
            style={{
              padding: '15px'
            }}
          >
            <ArrowRightCircle
              style={{
                height: '2.5rem',
                width: '2.5rem'
              }}
            />
          </div>
        </div>
        {/* <CardHeader
          className="card_title"
          style={{
            color: 'white',
            background: 'linear-gradient(45deg, black, transparent)',
            justifyContent: 'center',
            padding: '5px',
            marginBottom: '5px'
          }}
        >
         
        </CardHeader>
        <CardBody className="p-0 m-0 row">
          <div className="col card_image_div">
            <img
              src={this.props.background}
              alt="logo"
              className="card_image"
            />
          </div>
          <div className="col h-100">
            <div className="row mt-4">
              <span
                style={{
                  color: themeConfig.theme === 'dark' ? 'white' : 'black',
                  padding: '0px 10px 0px 25px'
                }}
                className="card_description text-center"
              >
                {this.props.statTitle}
              </span>
            </div>
          </div>
        </CardBody> */}
      </Card>
    )
  }
}
export default DashboardCards
